import { mapState } from "vuex";
import vHeader from "@/components/v-header";
import vCell from "@/components/v-cell";
export default {
  data() {
    return {
      walletInfo: '',
      noWallet: false
    };
  },
  components: {
    vHeader,
    "v-cell": vCell
  },
  computed: {
    ...mapState(["token"])
  },
  created() {
    this.verToken();
  },
  methods: {
    verToken(){
     if(!this.token){
      this.showTip("登录超时，请重新登录");
      this.$router.push({path: "/mine/login"});
     }else{
      // 获取我的钱包数据
      this.getWalletInfo();
     }
    },
    // 获取我的钱包数据
    getWalletInfo() {
      this.$axios
        .get(this.api.userWallet, { headers: { "Authorization": this.token } })
        .then(res => {
          const resData = res.data;
          if (resData.code !== 1) {
            this.noWallet = true;
            this.showTip(resData.message);
            return;
          }
          this.walletInfo = resData.content;
          console.log(this.walletInfo)
        })
        .catch(res => {
          this.noWallet = true;
          this.showTip("获取我的钱包数据失败");
        });
    }
  }
};



